"use client";

import React, { useState, useEffect, useRef, useCallback } from "react";
import { HeadlineInstruction } from "../../elements/HeadlineInstruction";
import { CardLight } from "../../elements/Card";
import { Button } from "../../elements/Button";
import FeedbackSuccessAnimation from "../FeedbackSuccessAnimation";
import BouncingMicrophone from "../../elements/BouncingMicrophone";
import { threeLetterWords } from "../../const/words";
import LoudspeakerIcon from "@/app/icons/LoudspeakerIcon";
import { ButtonSpeak } from "@/app/elements/ButtonSpeak";

// Type declarations for Web Speech API

interface SpeechRecognitionInstance {
  lang: string;
  interimResults: boolean;
  continuous: boolean;
  onresult: ((event: SpeechRecognitionEvent) => void) | null;
  onerror: ((event: SpeechRecognitionErrorEvent) => void) | null;
  onend: (() => void) | null;
  start: () => void;
  stop: () => void;
  abort: () => void;
}

interface SpeechRecognitionStatic {
  new (): SpeechRecognitionInstance;
}

declare global {
  interface Window {
    SpeechRecognition: SpeechRecognitionStatic;
    webkitSpeechRecognition: SpeechRecognitionStatic;
  }
}

interface SpeechRecognitionEvent {
  results: SpeechRecognitionResultList;
}

interface SpeechRecognitionResultList {
  [index: number]: SpeechRecognitionResult;
  length: number;
}

interface SpeechRecognitionResult {
  [index: number]: SpeechRecognitionAlternative;
  isFinal: boolean;
  length: number;
}

interface SpeechRecognitionAlternative {
  transcript: string;
  confidence: number;
}

interface SpeechRecognitionErrorEvent {
  error: string;
  message: string;
}

const GameBoardBlending: React.FC = () => {
  const [currentWord, setCurrentWord] = useState("");
  const [lettersOfCurrentWord, setLettersOfCurrentWord] = useState<string[]>(
    []
  );
  const [isListening, setIsListening] = useState(false);
  const [showSuccessContainer, setShowSuccessContainer] = useState(false);
  const [startSuccessAnimation, setStartSuccessAnimation] = useState(false);

  const instructionUtteranceRef = useRef<SpeechSynthesisUtterance | null>(null);
  const successSoundAudioRef = useRef<HTMLAudioElement | null>(null);
  const letterSoundAudioRef = useRef<HTMLAudioElement | null>(null);
  const recognitionRef = useRef<SpeechRecognitionInstance | null>(null);
  const resultReceivedRef = useRef(false);

  const successAppearTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const successDurationTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const successHideTimeoutRef = useRef<NodeJS.Timeout | null>(null);

  const successSoundFiles = Array.from(
    { length: 12 },
    (_, i) => `/sounds/success/success-${i + 1}.aac`
  );

  const clearAllTimeouts = useCallback(() => {
    if (successAppearTimeoutRef.current)
      clearTimeout(successAppearTimeoutRef.current);
    if (successDurationTimeoutRef.current)
      clearTimeout(successDurationTimeoutRef.current);
    if (successHideTimeoutRef.current)
      clearTimeout(successHideTimeoutRef.current);
  }, []);

  const playSuccessSound = useCallback(() => {
    if (letterSoundAudioRef.current) letterSoundAudioRef.current.pause();
    if (successSoundAudioRef.current) successSoundAudioRef.current.pause();

    const randomIndex = Math.floor(Math.random() * successSoundFiles.length);
    const soundToPlay = successSoundFiles[randomIndex];
    const audio = new Audio(soundToPlay);
    successSoundAudioRef.current = audio;
    audio.play().catch(console.error);
    audio.onended = () => {
      if (successSoundAudioRef.current === audio)
        successSoundAudioRef.current = null;
    };
    audio.onerror = () => {
      if (successSoundAudioRef.current === audio)
        successSoundAudioRef.current = null;
    };
  }, [successSoundFiles]);

  const playLetterSound = useCallback((letter: string) => {
    if (window.speechSynthesis.speaking) window.speechSynthesis.cancel();
    if (successSoundAudioRef.current) successSoundAudioRef.current.pause();

    const soundSrc = `/sounds/phonics/${letter.toLowerCase()}.m4a`;
    if (letterSoundAudioRef.current) {
      letterSoundAudioRef.current.pause();
      letterSoundAudioRef.current.onended = null;
      letterSoundAudioRef.current.onerror = null;
    }
    const audio = new Audio(soundSrc);
    letterSoundAudioRef.current = audio;
    audio.onended = () => {
      if (letterSoundAudioRef.current === audio)
        letterSoundAudioRef.current = null;
    };
    audio.onerror = () => {
      if (letterSoundAudioRef.current === audio)
        letterSoundAudioRef.current = null;
    };
    audio.play().catch((err) => {
      if (err.name !== "AbortError") console.error(err);
    });
  }, []);

  const generateNewWord = useCallback(() => {
    clearAllTimeouts();
    if (recognitionRef.current) {
      recognitionRef.current.abort();
      recognitionRef.current = null;
    }
    setShowSuccessContainer(false);
    setStartSuccessAnimation(false);
    setIsListening(false);

    const idx = Math.floor(Math.random() * threeLetterWords.length);
    const newWord = threeLetterWords[idx];
    setCurrentWord(newWord);
    setLettersOfCurrentWord(newWord.split(""));
  }, [clearAllTimeouts]);

  const speakWord = useCallback(
    (wordToSpeak: string) => {
      if (letterSoundAudioRef.current) letterSoundAudioRef.current.pause();
      if (successSoundAudioRef.current) successSoundAudioRef.current.pause();

      if (window.speechSynthesis) {
        if (window.speechSynthesis.speaking) window.speechSynthesis.cancel();
        const utt = new SpeechSynthesisUtterance(wordToSpeak);
        instructionUtteranceRef.current = utt;
        const voices = window.speechSynthesis.getVoices();
        const voice =
          voices.find(
            (v) => v.lang === "en-US" && v.name.toLowerCase().includes("female")
          ) || voices.find((v) => v.lang === "en-US");
        if (voice) utt.voice = voice;

        utt.onend = () => {
          instructionUtteranceRef.current = null;
          setTimeout(() => generateNewWord(), 100);
        };
        utt.onerror = () => {
          instructionUtteranceRef.current = null;
          setTimeout(() => generateNewWord(), 100);
        };
        window.speechSynthesis.speak(utt);
      } else {
        setTimeout(() => generateNewWord(), 100);
      }
    },
    [generateNewWord]
  );

  const stopRecognition = useCallback(() => {
    if (recognitionRef.current) recognitionRef.current.stop();
  }, []);

  const handleSpeechRecognition = useCallback(() => {
    if (letterSoundAudioRef.current) letterSoundAudioRef.current.pause();
    const API = window.SpeechRecognition || window.webkitSpeechRecognition;
    if (!API) {
      console.warn("Speech Recognition not supported");
      speakWord(currentWord);
      return;
    }

    if (recognitionRef.current) {
      recognitionRef.current.abort();
      recognitionRef.current = null;
    }

    resultReceivedRef.current = false;
    const recognition = new API();
    recognitionRef.current = recognition;
    recognition.lang = "en-US";
    recognition.interimResults = false;
    // Keep listening until user stops (e.g. release or evaluate)
    recognition.continuous = true;
    setIsListening(true);

    recognition.onresult = (evt) => {
      resultReceivedRef.current = true;
      const transcript = evt.results[0][0].transcript.trim().toLowerCase();
      console.log("transcript", transcript);
      console.log("currentWord", currentWord.toLowerCase());
      if (transcript.includes(currentWord.toLowerCase())) {
        setShowSuccessContainer(true);
        setStartSuccessAnimation(false);
        playSuccessSound();
        successAppearTimeoutRef.current = setTimeout(
          () => setStartSuccessAnimation(true),
          50
        );
        successDurationTimeoutRef.current = setTimeout(
          () => setStartSuccessAnimation(false),
          3050
        );
        successHideTimeoutRef.current = setTimeout(
          () => generateNewWord(),
          3350
        );
      } else {
        speakWord(currentWord);
      }
    };

    recognition.onerror = (evt) => {
      // Ignore abort errors triggered by manual stop
      if (evt.error === "aborted") {
        return;
      }
      resultReceivedRef.current = true;
      console.error("Speech Recognition Error:", evt.error, evt.message);
      speakWord(currentWord);
    };

    recognition.onend = () => {
      setIsListening(false);
      if (recognitionRef.current === recognition) recognitionRef.current = null;
      if (!resultReceivedRef.current) {
        // User clicked evaluate without speaking
        speakWord(currentWord);
      }
    };

    try {
      recognition.start();
    } catch (e) {
      console.error(e);
      setIsListening(false);
      if (recognitionRef.current === recognition) recognitionRef.current = null;
      speakWord(currentWord);
    }
  }, [currentWord, playSuccessSound, generateNewWord, speakWord]);

  const triggerListen = useCallback(() => {
    if (isListening) {
      stopRecognition();
    } else {
      if (window.speechSynthesis && window.speechSynthesis.speaking)
        window.speechSynthesis.cancel();
      if (letterSoundAudioRef.current) letterSoundAudioRef.current.pause();
      handleSpeechRecognition();
    }
  }, [isListening, stopRecognition, handleSpeechRecognition]);

  useEffect(() => {
    generateNewWord();
    return () => {
      clearAllTimeouts();
      if (recognitionRef.current) recognitionRef.current.abort();
      if (instructionUtteranceRef.current && window.speechSynthesis)
        window.speechSynthesis.cancel();
      if (successSoundAudioRef.current) successSoundAudioRef.current.pause();
      if (letterSoundAudioRef.current) letterSoundAudioRef.current.pause();
    };
  }, [generateNewWord, clearAllTimeouts]);

  useEffect(() => {
    const onKeyDown = (e: KeyboardEvent) => {
      if (e.code === "Space" && !e.repeat) {
        e.preventDefault();
        if (
          !isListening &&
          !showSuccessContainer &&
          !["INPUT", "TEXTAREA"].includes(document.activeElement?.tagName || "")
        ) {
          triggerListen();
        }
      }
    };
    const onKeyUp = (e: KeyboardEvent) => {
      if (e.code === "Space") {
        if (isListening && recognitionRef.current) stopRecognition();
      }
    };
    window.addEventListener("keydown", onKeyDown);
    window.addEventListener("keyup", onKeyUp);
    return () => {
      window.removeEventListener("keydown", onKeyDown);
      window.removeEventListener("keyup", onKeyUp);
    };
  }, [triggerListen, isListening, showSuccessContainer, stopRecognition]);

  return (
    <div className="h-full w-full flex flex-col items-center justify-center relative">
      {isListening && (
        <div className="absolute bottom-[25%] left-1/2 -translate-x-1/2 z-30">
          <Button
            text="Evaluate Now"
            onClick={stopRecognition}
            className="px-6 py-3 text-lg font-semibold bg-green-500 hover:bg-green-600 text-white focus:ring-green-400"
            aria-label="Evaluate speech input now"
          />
        </div>
      )}
      <BouncingMicrophone isVisible={isListening} />
      {showSuccessContainer && (
        <div className="absolute inset-0 flex flex-col items-center justify-center z-20">
          <FeedbackSuccessAnimation show={startSuccessAnimation} />
        </div>
      )}

      <div
        className={`h-auto max-h-[80vh] mb-10 flex flex-col items-center transition-opacity duration-300 ${
          isListening ? "opacity-30 pointer-events-none" : "opacity-100"
        }`}
      >
        <HeadlineInstruction
          headlineText="Blend the sounds to make a word!"
          instructionText="Click letters to hear sounds. Click button or press Space to speak. Release Space or click Evaluate to finish."
          className={`${
            showSuccessContainer && startSuccessAnimation
              ? "opacity-0"
              : "opacity-100"
          } transition-opacity duration-300`}
        />
        <CardLight
          className={`${
            showSuccessContainer && startSuccessAnimation
              ? "opacity-0"
              : "opacity-100"
          }`}
        >
          <div className="flex justify-center items-center space-x-2 md:space-x-4 my-8 px-4">
            {lettersOfCurrentWord.map((letter, idx) => (
              <button
                key={`${currentWord}-${idx}`}
                onClick={() => playLetterSound(letter)}
                disabled={isListening || showSuccessContainer}
                aria-label={`Play sound for letter ${letter}`}
                className="text-[12rem] leading-none font-bold text-fuchsia-950 focus:outline-none rounded-lg p-2 transition-transform hover:scale-105 active:scale-95 disabled:opacity-50"
              >
                {idx === 0 ? letter : letter.toLowerCase()}
              </button>
            ))}
          </div>
          <div className="flex justify-center mt-8 mb-4">
            <ButtonSpeak
              onClick={triggerListen}
              disabled={isListening || showSuccessContainer}
              text={isListening ? "Listening..." : "Speak Word"}
              aria-label="Speak the word"
              className="px-8 py-4 text-xl font-semibold"
              icon={LoudspeakerIcon}
            />
          </div>
        </CardLight>
      </div>
    </div>
  );
};

export default GameBoardBlending;
